body{
background-color: #02e5ed;
}


.logoimg{
  margin-top: 3%;
  height: 22%;
  width: 100%;
  position: absolute;
  left:0;
}

.locatmainbutton{
    position: absolute;
    right:    9%;
    bottom:   6%;

}
.mainbutton{
  display:block;
  height: 66px;
  width: 66px;
  border-radius: 50%;
  border: 1px solid white;
  background-color: #f90680;
  color: white;
  position: fixed;

  right: 8%;
  bottom: 6%;
}

.morebutton{
  display:block;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border: 1px solid white;
  background-color: #f90680;
  color: white;
}
what2do{
  font-family: 'Avenir Next';
  color: black;
  font-size: 30px;
  margin-left: 3%;

}
profilbutton{
  position: fixed;
  right: 3%;
  bottom: 17.5%;
  z-index: 1;
}
mapsbutton{
  position: fixed;
  right: 19%;
  bottom: 17.5%;
  z-index: 1;
}
filterbutton{
  position: fixed;
  right:    29%;
  bottom:   2%;
  z-index: 1;
}

settingbutton{
  position: fixed;
  right: 29%;
  bottom: 10.5%;
  z-index: 1;
}


.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
